import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Separator } from "@/components/ui/separator";
import { Progress } from "@/components/ui/progress";
import {
  Phone,
  MessageSquare,
  Calendar,
  DollarSign,
  TrendingUp,
  CheckCircle,
  AlertCircle,
  Clock,
  ArrowLeft,
  FileText,
  Download,
} from "lucide-react";
import { Metadata } from "next";

export const metadata: Metadata = {
  title: "Renewal Profile",
  description: "Client renewal profile details",
};

const RenewalProfile = () => {
  // Mock renewal profile data
  const profile = {
    id: 1,
    clientName: "Michael Johnson",
    phone: "(555) 123-4567",
    email: "michael.johnson@email.com",
    profileImage: "https://images.pexels.com/photos/1681010/pexels-photo-1681010.jpeg",
    currentMortgage: "$580,000",
    rate: "5.25%",
    lender: "TD Bank",
    originalDate: "2022-10-15",
    renewalDate: "2025-10-15",
    status: "overdue",
    daysOverdue: 2,
    priority: "high",
    lastContactDate: "2025-09-10",
    nextFollowUp: "2025-10-13",
    mortgageType: "Fixed Rate",
    amortization: "25 years",
    property: "123 Oak Street, Toronto, ON",
    notes:
      "Client prefers morning calls. Very rate-sensitive. Has mentioned interest in home equity line of credit.",
    interactions: [
      {
        date: "2025-09-10",
        type: "Call",
        duration: "12 min",
        notes: "Initial contact - Discussed renewal options",
        outcome: "Interested in rate comparison",
      },
      {
        date: "2025-09-05",
        type: "Email",
        duration: "-",
        notes: "Sent renewal information package",
        outcome: "Awaiting response",
      },
      {
        date: "2025-08-28",
        type: "Call",
        duration: "8 min",
        notes: "Reminder about upcoming renewal",
        outcome: "Confirmed contact info",
      },
    ],
    crossSells: [
      { id: 1, product: "HELOC", potential: "$150,000", status: "interested" },
      {
        id: 2,
        product: "Insurance Review",
        potential: "Premium review",
        status: "pending",
      },
      { id: 3, product: "Investment Services", potential: "RRSP", status: "not_discussed" },
    ],
  };

  return (
    <div className="p-6 bg-gray-50 min-h-screen">
      <div className="max-w-6xl mx-auto">
        {/* HEADER WITH BACK BUTTON */}
        <div className="flex items-center gap-3 mb-6">
          <Button variant="outline" size="icon">
            <ArrowLeft className="w-4 h-4" />
          </Button>
          <h1 className="text-3xl font-bold">Renewal Profile</h1>
        </div>

        <div className="grid grid-cols-3 gap-6">
          {/* LEFT COLUMN - MAIN INFO */}
          <div className="col-span-2 space-y-6">
            {/* CLIENT CARD */}
            <Card className="bg-primary-foreground border-0 shadow-sm">
              <CardContent className="p-6">
                <div className="flex items-start justify-between mb-6">
                  <div className="flex items-start gap-4">
                    <div className="w-16 h-16 rounded-full bg-gradient-to-br from-blue-400 to-purple-500 flex items-center justify-center text-white text-2xl font-bold">
                      MJ
                    </div>
                    <div>
                      <div className="flex items-center gap-2 mb-2">
                        <h2 className="text-2xl font-bold">{profile.clientName}</h2>
                        <Badge
                          className={`${
                            profile.status === "overdue"
                              ? "bg-red-100 text-red-800"
                              : profile.status === "due"
                              ? "bg-orange-100 text-orange-800"
                              : "bg-blue-100 text-blue-800"
                          }`}
                        >
                          {profile.status === "overdue"
                            ? `${profile.daysOverdue} Days Overdue`
                            : "Due Soon"}
                        </Badge>
                        <Badge className="bg-red-500 text-white">
                          {profile.priority.charAt(0).toUpperCase() + profile.priority.slice(1)}{" "}
                          Priority
                        </Badge>
                      </div>
                      <div className="flex gap-4 text-sm text-gray-600">
                        <span>{profile.phone}</span>
                        <span>•</span>
                        <span>{profile.email}</span>
                      </div>
                    </div>
                  </div>
                  <div className="flex gap-2">
                    <Button className="bg-green-600 hover:bg-green-700 gap-2">
                      <Phone className="w-4 h-4" />
                      Call Now
                    </Button>
                    <Button variant="outline" className="gap-2">
                      <MessageSquare className="w-4 h-4" />
                      Message
                    </Button>
                  </div>
                </div>

                <Separator className="my-4" />

                {/* MORTGAGE DETAILS GRID */}
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <p className="text-sm text-gray-600 mb-1">Current Mortgage</p>
                    <p className="text-2xl font-bold text-blue-600">{profile.currentMortgage}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600 mb-1">Current Rate</p>
                    <p className="text-2xl font-bold">{profile.rate}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600 mb-1">Mortgage Type</p>
                    <p className="font-semibold">{profile.mortgageType}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600 mb-1">Amortization</p>
                    <p className="font-semibold">{profile.amortization}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600 mb-1">Lender</p>
                    <p className="font-semibold">{profile.lender}</p>
                  </div>
                  <div>
                    <p className="text-sm text-gray-600 mb-1">Property</p>
                    <p className="font-semibold text-sm">{profile.property}</p>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* RENEWAL TIMELINE */}
            <Card className="bg-primary-foreground border-0 shadow-sm">
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <Calendar className="w-5 h-5" />
                  Renewal Timeline
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-6">
                  <div className="flex gap-4">
                    <div className="flex flex-col items-center">
                      <div className="w-10 h-10 rounded-full bg-green-500 text-white flex items-center justify-center">
                        <CheckCircle className="w-5 h-5" />
                      </div>
                      <div className="w-0.5 h-12 bg-gray-300 mt-2"></div>
                    </div>
                    <div className="pb-8">
                      <p className="font-semibold">Original Mortgage Date</p>
                      <p className="text-sm text-gray-600">{profile.originalDate}</p>
                    </div>
                  </div>

                  <div className="flex gap-4">
                    <div className="flex flex-col items-center">
                      <div className="w-10 h-10 rounded-full bg-red-500 text-white flex items-center justify-center">
                        <AlertCircle className="w-5 h-5" />
                      </div>
                      <div className="w-0.5 h-12 bg-gray-300 mt-2"></div>
                    </div>
                    <div className="pb-8">
                      <p className="font-semibold">Renewal Date (Overdue)</p>
                      <p className="text-sm text-gray-600">{profile.renewalDate}</p>
                      <p className="text-xs text-red-600 mt-1">
                        ⚠️ {profile.daysOverdue} days overdue
                      </p>
                    </div>
                  </div>

                  <div className="flex gap-4">
                    <div className="flex flex-col items-center">
                      <div className="w-10 h-10 rounded-full bg-blue-500 text-white flex items-center justify-center">
                        <Clock className="w-5 h-5" />
                      </div>
                    </div>
                    <div>
                      <p className="font-semibold">Next Follow-up</p>
                      <p className="text-sm text-gray-600">{profile.nextFollowUp}</p>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* INTERACTION HISTORY */}
            <Card className="bg-primary-foreground border-0 shadow-sm">
              <CardHeader>
                <CardTitle>Interaction History</CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {profile.interactions.map((interaction, idx) => (
                    <div key={idx} className="border-l-4 border-blue-400 pl-4 py-2">
                      <div className="flex justify-between items-start">
                        <div>
                          <p className="font-semibold flex items-center gap-2">
                            {interaction.type}
                            {interaction.duration !== "-" && (
                              <span className="text-xs bg-gray-100 px-2 py-1 rounded">
                                {interaction.duration}
                              </span>
                            )}
                          </p>
                          <p className="text-sm text-gray-600 mt-1">{interaction.notes}</p>
                          <p className="text-xs text-gray-500 mt-2">
                            Outcome: {interaction.outcome}
                          </p>
                        </div>
                        <span className="text-sm text-gray-500 flex-shrink-0">
                          {interaction.date}
                        </span>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>

            {/* CROSS-SELL OPPORTUNITIES */}
            <Card className="bg-primary-foreground border-0 shadow-sm">
              <CardHeader>
                <CardTitle className="flex items-center gap-2">
                  <TrendingUp className="w-5 h-5" />
                  Cross-Sell Opportunities
                </CardTitle>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {profile.crossSells.map((item) => (
                    <div
                      key={item.id}
                      className="flex items-center justify-between p-3 bg-gray-50 rounded-lg"
                    >
                      <div>
                        <p className="font-medium">{item.product}</p>
                        <p className="text-sm text-gray-600">{item.potential}</p>
                      </div>
                      <Badge
                        className={
                          item.status === "interested"
                            ? "bg-green-100 text-green-800"
                            : item.status === "pending"
                            ? "bg-yellow-100 text-yellow-800"
                            : "bg-gray-100 text-gray-800"
                        }
                      >
                        {item.status.charAt(0).toUpperCase() +
                          item.status.slice(1).replace("_", " ")}
                      </Badge>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </div>

          {/* RIGHT COLUMN - SIDEBAR */}
          <div className="space-y-6">
            {/* NOTES CARD */}
            <Card className="bg-primary-foreground border-0 shadow-sm">
              <CardHeader>
                <CardTitle className="text-base flex items-center gap-2">
                  <FileText className="w-4 h-4" />
                  Notes
                </CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-sm text-gray-700">{profile.notes}</p>
              </CardContent>
            </Card>

            {/* KEY METRICS */}
            <Card className="bg-primary-foreground border-0 shadow-sm">
              <CardHeader>
                <CardTitle className="text-base">Key Metrics</CardTitle>
              </CardHeader>
              <CardContent className="space-y-4">
                <div>
                  <div className="flex justify-between mb-2">
                    <span className="text-sm font-medium">Renewal Urgency</span>
                    <span className="text-sm text-red-600 font-semibold">Critical</span>
                  </div>
                  <Progress value={100} className="h-2" />
                </div>

                <div>
                  <div className="flex justify-between mb-2">
                    <span className="text-sm font-medium">Engagement Level</span>
                    <span className="text-sm text-blue-600 font-semibold">High</span>
                  </div>
                  <Progress value={75} className="h-2" />
                </div>

                <div>
                  <div className="flex justify-between mb-2">
                    <span className="text-sm font-medium">Cross-Sell Potential</span>
                    <span className="text-sm text-green-600 font-semibold">Medium</span>
                  </div>
                  <Progress value={60} className="h-2" />
                </div>
              </CardContent>
            </Card>

            {/* QUICK ACTIONS */}
            <Card className="bg-primary-foreground border-0 shadow-sm">
              <CardHeader>
                <CardTitle className="text-base">Quick Actions</CardTitle>
              </CardHeader>
              <CardContent className="space-y-2">
                <Button className="w-full justify-start bg-blue-600 hover:bg-blue-700">
                  <Phone className="w-4 h-4 mr-2" />
                  Schedule Call
                </Button>
                <Button className="w-full justify-start" variant="outline">
                  <FileText className="w-4 h-4 mr-2" />
                  Generate Proposal
                </Button>
                <Button className="w-full justify-start" variant="outline">
                  <Download className="w-4 h-4 mr-2" />
                  Download Documents
                </Button>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    </div>
  );
};

export default RenewalProfile;